import React from 'react';
import PaymentScreen from '../PaymentScreen';
import {render, waitFor} from '@testing-library/react-native';
import '@testing-library/jest-native/extend-expect';
import {NativeStackNavigationProp} from '@react-navigation/native-stack';
import {useNavigation} from '@react-navigation/native';

jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});
const calculateChargesMock = jest.fn();
describe('Payment Screen/Deregister Parking', () => {
  it('displays the time spent', () => {
    const details = {
      reg: 'ABC123',
      time: new Date(Date.now() - 60 * 60 * 1000).toISOString(),
    };
    const route = {params: {details}};
    const {getByTestId} = render(<PaymentScreen route={route} />);
    const timeSpentText = getByTestId('deregester-time-spent').props
      .children[1];
    expect(timeSpentText).toBe('1 hours 0 minutes');
  });
});
